const sanitizeCombatLogLine = (line: string) => {
  return line
    .replace(/<[^>]*>/g, '')
    .replace(/&nbsp;/g, ' ')
    .replace(/\s+/g, ' ')
    .trim();
};

const formatCombatLogs = (logs: string[]) => {
  const ordered = [...logs]
    .reverse()
    .map(sanitizeCombatLogLine)
    .filter((line) => line.length > 0);
  if (ordered.length === 0) return 'ÔºàÊàòÊñóÊó•Âøó‰∏∫Á©∫Ôºâ';
  return ordered.join('\n');
};

