interface PortalChoice {
  label: string;
  roomType: string;
  areaName?: string;
  floorName?: string;
  isFloorTransition: boolean;
  icon: string;
  bgColor: string;
  borderColor: string;
  textColor: string;
  glowColor: string;
}

let cachedPortals: PortalChoice[] = [];
let cachedPortalFingerprint = '';

function shuffle<T>(arr: T[]): T[] {
  const a = [...arr];
  for (let i = a.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [a[i], a[j]] = [a[j], a[i]];
  }
  return a;
}

function rollPortalCount(): number {
  const roll = Math.random();
  // ‰º†ÈÄÅÈó®Êï∞ÈáèÊ¶ÇÁéáÔºö1/2/3 = 30% / 50% / 20%
  return roll < 0.3 ? 1 : roll < 0.8 ? 2 : 3;
}

function pickWeightedRoomTypes(roomTypes: string[], count: number): string[] {
  const picked: string[] = [];
  if (roomTypes.length === 0 || count <= 0) return picked;

  // ÂÖÅËÆ∏ÂèØÈáçÂ§çÊäΩÂèñÔºöÊØèÊ¨°ÈÉΩ‰ªéÂêå‰∏ÄÂÄôÈÄâÊ±†ÊåâÊùÉÈáçÊäΩÂèñÔºå‰∏çÁßªÈô§Â∑≤ÊäΩ‰∏≠ÁöÑÊàøÈó¥Á±ªÂûã
  const weightedPool = roomTypes.map((type) => ({
    type,
    weight: Math.max(0, PORTAL_ROOM_WEIGHTS[type] ?? 0),
  }));
  const totalWeight = weightedPool.reduce((sum, item) => sum + item.weight, 0);

  while (picked.length < count) {
    let selected: string;
    if (totalWeight <= 0) {
      selected = pickOne(roomTypes) ?? roomTypes[0]!;
    } else {
      let roll = Math.random() * totalWeight;
      selected = weightedPool[weightedPool.length - 1]!.type;
      for (const item of weightedPool) {
        roll -= item.weight;
        if (roll <= 0) {
          selected = item.type;
          break;
        }
      }
    }

    picked.push(selected);
  }

  return picked;
}

function generatePortals(): PortalChoice[] {
  const currentArea = (gameStore.statData._ÂΩìÂâçÂå∫Âüü as string) || '';
  const currentRoomType = (gameStore.statData._ÂΩìÂâçÊàøÈó¥Á±ªÂûã as string) || '';
  const stats = (gameStore.statData.$ÁªüËÆ° as any) || {};
  const roomsPassed: number = stats.ÂΩìÂâçÂ±ÇÂ∑≤ËøáÊàøÈó¥ ?? 0;

  const isStartArea = currentArea === 'È≠îÂ•≥ÁöÑÂ∞èÁ™ù';
  const isBossRoom = currentRoomType === 'È¢Ü‰∏ªÊàø';

  // ‚îÄ‚îÄ Special: È≠îÂ•≥ÁöÑÂ∞èÁ™ù or È¢Ü‰∏ªÊàø ‚Üí next floor area portals ‚îÄ‚îÄ
  if (isStartArea || isBossRoom) {
    let targetFloor: string | null = null;
    if (isStartArea) {
      targetFloor = 'Á¨¨‰∏ÄÂ±Ç';
    } else {
      const currentFloor = getFloorForArea(currentArea);
      if (currentFloor) targetFloor = getNextFloor(currentFloor);
    }
    if (targetFloor && FLOOR_MAP[targetFloor]) {
      const candidates = FLOOR_MAP[targetFloor].filter(a => a !== currentArea);
      const picked = shuffle(candidates).slice(0, 3);
      return picked.map(areaName => ({
        label: areaName,
        roomType: '',
        areaName,
        floorName: targetFloor!,
        isFloorTransition: true,
        ...AREA_PORTAL_VISUAL,
      }));
    }
  }

  // ‚îÄ‚îÄ Boss room probability: rooms >= 8 ‚Üí (rooms - 7) * 20% ‚îÄ‚îÄ
  if (roomsPassed >= 8) {
    const bossChance = (roomsPassed - 7) * 0.2;
    if (Math.random() < bossChance) {
      const vis = PORTAL_ROOM_VISUALS['È¢Ü‰∏ªÊàø'];
      return [{ label: 'È¢Ü‰∏ªÊàø', roomType: 'È¢Ü‰∏ªÊàø', isFloorTransition: false, ...vis }];
    }
  }

  // ‚îÄ‚îÄ Normal: 1-3 weighted room portals (30%/50%/20%, with replacement) ‚îÄ‚îÄ
  const availableRoomTypes = getAvailablePortalRoomTypes(currentArea);
  if (availableRoomTypes.length === 0) return [];
  const count = rollPortalCount();
  const picked = pickWeightedRoomTypes(availableRoomTypes, count);
  return picked.map(rt => ({ label: rt, roomType: rt, isFloorTransition: false, ...PORTAL_ROOM_VISUALS[rt] }));
}

// ‰ΩøÁî®Áä∂ÊÄÅÊåáÁ∫πÂÆûÁé∞ÂìçÂ∫îÂºèÊõ¥Êñ∞ÔºöÂΩìÂå∫Âüü/ÊàøÈó¥Á±ªÂûã/hasLeave ÂèòÂåñÊó∂ÈáçÊñ∞ÁîüÊàê‰º†ÈÄÅÈó®
const portalChoices = computed<PortalChoice[]>(() => {
  if (!gameStore.hasLeave) {
    cachedPortals = [];
    cachedPortalFingerprint = '';
    return [];
  }
  // ÊûÑÂª∫Áä∂ÊÄÅÊåáÁ∫πÔºöÂå∫Âüü + ÊàøÈó¥Á±ªÂûã + ÁªüËÆ°Ôºå‰ªª‰ΩïÂèòÂåñÈÉΩÈáçÊñ∞ÁîüÊàê
  const area = (gameStore.statData._ÂΩìÂâçÂå∫Âüü as string) || '';
  const roomType = (gameStore.statData._ÂΩìÂâçÊàøÈó¥Á±ªÂûã as string) || '';
  const rooms = ((gameStore.statData.$ÁªüËÆ° as any)?.ÂΩìÂâçÂ±ÇÂ∑≤ËøáÊàøÈó¥ ?? 0);
  const merchantDefeated = parseMerchantDefeatedValue(gameStore.statData._ÊòØÂê¶Â∑≤ÂáªË¥•ÂïÜ‰∫∫);
  const fingerprint = `${area}|${roomType}|${rooms}|${merchantDefeated ? 1 : 0}`;
  if (fingerprint !== cachedPortalFingerprint) {
    cachedPortalFingerprint = fingerprint;
    cachedPortals = generatePortals();
  }
  return cachedPortals;
});

