  'Ê∏©Ê≥âÊàø': 'Á¥ØËÆ°ÁªèËøáÊ∏©Ê≥â', 'Á•ûÂÉèÊàø': 'Á¥ØËÆ°ÁªèËøáÁ•ûÂÉè', '‰∫ã‰ª∂Êàø': 'Á¥ØËÆ°ÁªèËøá‰∫ã‰ª∂', 'Èô∑Èò±Êàø': 'Á¥ØËÆ°ÁªèËøáÈô∑Èò±',
