const buildQueuedPortalAction = (portal: PortalChoice): QueuedPortalAction => {
  if (portal.isFloorTransition) {
    // ËÆ∞ÂΩïÂæÖÂ∫îÁî®ÂèòÈáèÔºöËøõÂÖ•Êñ∞Âå∫ÂüüÔºåÈ¶ñ‰∏™ÊàøÈó¥‰∏∫ÂÆùÁÆ±ÊàøÔºåÈáçÁΩÆÊàøÈó¥ËÆ°Êï∞
    gameStore.setPendingPortalChanges({
      area: portal.areaName!,
      roomType: 'ÂÆùÁÆ±Êàø',
      resetRoomCounter: true,
      // Êñ∞Âå∫ÂüüÈ¶ñ‰∏™ÊàøÈó¥ÂêåÊ†∑ËÆ°ÂÖ•ÁªüËÆ°ÔºöÂÆùÁÆ±Êàø +1„ÄÅÁ¥ØËÆ°ÊÄªÊàøÈó¥ +1„ÄÅÂΩìÂ±ÇÊàøÈó¥ +1
      incrementKeys: ['ÂΩìÂâçÂ±ÇÂ∑≤ËøáÊàøÈó¥', 'Á¥ØËÆ°Â∑≤ËøáÊàøÈó¥', 'Á¥ØËÆ°ÁªèËøáÂÆùÁÆ±'],
      enemyName: '',
    });
    console.info(`[Portal] Floor transition queued ‚Üí area: ${portal.areaName}, first room: ÂÆùÁÆ±Êàø`);
    const enterText = `ËøõÂÖ•‰∫Ü${portal.areaName}ÁöÑÂÆùÁÆ±Êàø`;
    return {
      enterText,
      actionText: `<user>ÈÄâÊã©‰∫ÜÁªßÁª≠ÂâçËøõÔºå${enterText}`,
    };
  }

  // ËÆ∞ÂΩïÂæÖÂ∫îÁî®ÂèòÈáèÔºöËøõÂÖ•Êñ∞ÊàøÈó¥ÔºåÊõ¥Êñ∞ÁªüËÆ°
  const incrementKeys = ['ÂΩìÂâçÂ±ÇÂ∑≤ËøáÊàøÈó¥', 'Á¥ØËÆ°Â∑≤ËøáÊàøÈó¥'];
  const statKey = ROOM_STAT_KEY[portal.roomType];
  if (statKey) incrementKeys.push(statKey);
  const currentArea = (gameStore.statData._ÂΩìÂâçÂå∫Âüü as string) || '';
  const encounterMonster = portal.roomType === 'È¢Ü‰∏ªÊàø'
    ? pickLordMonsterByArea(currentArea)
    : portal.roomType === 'ÊàòÊñóÊàø'
      ? pickBattleMonsterByArea(currentArea)
      : null;
  const trapName = portal.roomType === 'Èô∑Èò±Êàø'
    ? pickTrapByArea(currentArea)
    : null;
  const trapHpAfterDamage = portal.roomType === 'Èô∑Èò±Êàø'
    ? Math.max(1, toNonNegativeInt(gameStore.statData._Ë°ÄÈáè, 1) - 5)
    : undefined;
  let pendingStatDataFields: Record<string, any> | undefined;
  if (portal.roomType === 'Èô∑Èò±Êàø') {
    pendingStatDataFields = {
      _ÂΩìÂâç‰∫ã‰ª∂: trapName ?? '',
      _Ë°ÄÈáè: trapHpAfterDamage,
    };
  } else if (portal.roomType === 'Ê∏©Ê≥âÊàø') {
    const maxHp = Math.max(
      1,
      toNonNegativeInt(gameStore.statData._Ë°ÄÈáè‰∏äÈôê, toNonNegativeInt(gameStore.statData._Ë°ÄÈáè, 1)),
    );
    pendingStatDataFields = {
      _Ë°ÄÈáè: maxHp,
    };
  }

  gameStore.setPendingPortalChanges({
    roomType: portal.roomType,
    incrementKeys,
    enemyName: encounterMonster ?? '',
  });
  console.info(`[Portal] Room transition queued ‚Üí type: ${portal.roomType}`);

  const enterText = (portal.roomType === 'ÊàòÊñóÊàø' || portal.roomType === 'È¢Ü‰∏ªÊàø') && encounterMonster
    ? `ËøõÂÖ•‰∫Ü${portal.roomType}Âπ∂ÈÅ≠ÈÅá‰∫Ü${encounterMonster}`
    : portal.roomType === 'Èô∑Èò±Êàø' && trapName
      ? `ËøõÂÖ•‰∫Ü${portal.roomType}ÁöÑÊàøÈó¥ÔºåÂΩìÂâçÈô∑Èò±Êàø‰∏∫${trapName}`
      : `ËøõÂÖ•‰∫Ü${portal.roomType}ÁöÑÊàøÈó¥`;

  return {
    enterText,
    actionText: `<user>ÈÄâÊã©‰∫ÜÁªßÁª≠ÂâçËøõÔºå${enterText}`,
    pendingStatDataFields,
  };
};

function generateChestLeavePortals(): PortalChoice[] {
  const generated = generatePortals();
  if (generated.length > 0) return generated;

  const currentArea = (gameStore.statData._ÂΩìÂâçÂå∫Âüü as string) || '';
  const availableRoomTypes = getAvailablePortalRoomTypes(currentArea);
  if (availableRoomTypes.length === 0) return [];
  const count = rollPortalCount();
  const picked = pickWeightedRoomTypes(availableRoomTypes, count);
  return picked.map(rt => ({ label: rt, roomType: rt, isFloorTransition: false, ...PORTAL_ROOM_VISUALS[rt] }));
}

const handlePortalClick = async (portal: PortalChoice) => {
  if (gameStore.isGenerating) return;
  const { actionText, pendingStatDataFields } = buildQueuedPortalAction(portal);
  gameStore.setPendingStatDataChanges(pendingStatDataFields ?? null);
  gameStore.sendAction(actionText);
};

const handleChestPortalClick = async (portal: PortalChoice) => {
  if (gameStore.isGenerating || chestCollecting.value || chestStage.value !== 'opened') return;
  const { actionText, enterText, pendingStatDataFields } = buildQueuedPortalAction(portal);
  const collectedRelic = chestRewardCollected.value ? chestRewardRelic.value : null;
  const mergedPendingStatDataFields: Record<string, any> = {
    ...(pendingStatDataFields ?? {}),
  };
  if (collectedRelic) {
    // ÂÆùÁÆ±Â•ñÂä±ÈÅµÂæ™‰∏é‰º†ÈÄÅÈó®‰∏ÄËá¥ÁöÑ‚ÄúÂª∂ËøüÂÜôÂÖ•‚ÄùÁ≠ñÁï•Ôºö‰ªÖÂú®ÁÇπÂáª‰º†ÈÄÅÈó®Êó∂ÊéíÈòüÂà∞‰∏ã‰∏ÄÂ±Ç user Ê•ºÂ±Ç
    const nextRelics = buildNextRelicInventory(collectedRelic);
    mergedPendingStatDataFields._Âú£ÈÅóÁâ© = nextRelics;
  }
  gameStore.setPendingStatDataChanges(Object.keys(mergedPendingStatDataFields).length > 0 ? mergedPendingStatDataFields : null);

  const relicName = collectedRelic?.name ?? '';
  closeChestView();
  if (relicName) {
    gameStore.sendAction(`<user>ÊâìÂºÄ‰∫ÜÁÆ±Â≠êÂπ∂‰ªé‰∏≠Ëé∑Âèñ‰∫ÜÂú£ÈÅóÁâ©${relicName}ÔºåÈöèÂêé${enterText}`);
    return;
  }
  gameStore.sendAction(actionText);
};


